export const AcceptButton = forwardRef<
  HTMLButtonElement,
  React.ButtonHTMLAttributes<HTMLButtonElement>
>((props, ref) => {
  const { className: addedClasses, ...restProps } = props;
  return (
    <button
      {...restProps}
      ref={ref}
      className={`rounded-lg border border-green-300 bg-green-300 px-3 py-1 text-center text-sm font-medium text-zinc-800 shadow-sm transition-all hover:border-green-700 hover:bg-green-700 hover:text-zinc-100 focus:ring focus:ring-green-200 disabled:cursor-not-allowed disabled:border-green-300 disabled:bg-green-200 disabled:text-green-600${
        addedClasses ? ` ${addedClasses}` : ''
      }`}/>
  );
});

