type MyRemixLinkProps = {
  variant?: 'gray' | 'red' | 'green' | 'primary' | 'amber' | 'purple';
  to: { pathname: string; searchParams?: Record<string, string> } | string;
} & RemixLinkProps;

export const LinkButton = forwardRef<HTMLAnchorElement, MyRemixLinkProps>(
  ({ variant = 'primary', to, ...props }, ref) => {
    const searchParams = useGenerateSearchParams({
      keysToRemove: 'type',
      keysToAdd: typeof to !== 'string' ? to.searchParams : undefined,
    });
    let colors = '';

    switch (variant) {
      case 'primary':
        colors =
          'border-primary-300 bg-primary-50 text-primary-800 hover:bg-primary-500 hover:border-primary-500 hover:text-secondary-50 focus:ring-primary-50 disabled:border-primary-50 disabled:bg-primary-50 disabled:text-primary-400';
        break;
      case 'green':
        colors =
          'border-green-500 bg-green-500 text-white hover:bg-green-700 hover:border-green-700 focus:ring-green-200 disabled:border-green-300 disabled:bg-green-300';
        break;
      case 'gray':
        colors =
          'border-gray-300 bg-white hover:bg-gray-400 hover:text-white focus:ring-gray-100 disabled:border-gray-100 disabled:bg-gray-50 disabled:text-gray-400';
        break;
      case 'red':
        colors =
          'border-red-200 bg-red-50 text-red-800 hover:border-red-700 hover:bg-red-700 hover:text-white focus:ring-red-200 disabled:border-red-300 disabled:bg-red-300';
        break;
      case 'amber':
        colors =
          'border-amber-200 bg-amber-50 text-amber-800 hover:border-amber-700 hover:bg-amber-500 hover:text-white focus:ring-amber-200 disabled:border-amber-300 disabled:bg-amber-300';            
        break;
      case 'purple':
        colors =
          'border-purple-200 bg-purple-50 text-purple-800 hover:border-purple-700 hover:bg-purple-500 hover:text-white focus:ring-purple-200 disabled:border-purple-300 disabled:bg-purple-300';
        break;
    }

    return (
      <Link
        {...props}
        to={{
          pathname: typeof to === 'string' ? to : to.pathname,
          search: searchParams,
        }}
        ref={ref}
        className={`${colors} rounded-lg border px-3 py-1 text-center text-sm font-medium shadow-sm transition-all focus:ring focus:ring-gray-100 disabled:cursor-not-allowed${props.className ? ` ${props.className}` : ''}`}/>
    );
  }
);
